/* eslint-disable no-undef */
import API_ENDPOINT from '../globals/api-endpoint';

class restaurantSource {
  static async allRestoData() {
    const loadingIndicator = document.getElementById('loadingIndicator');
    loadingIndicator.style.display = 'block';
    try {
      const response = await fetch(API_ENDPOINT.restoData);
      if (!response.ok) {
        throw new Error('Failed to fetch data');
      }
      const responseJson = await response.json();
      return responseJson.restaurants;
    } catch (error) {
      console.error('Error fetching data:', error);
      Swal.fire({
        icon: 'error',
        title: 'Error',
        text: 'Failed to fetch data. Please try again later.',
      });
      throw error;
    } finally {
      loadingIndicator.style.display = 'none';
    }
  }

  static async detailRestaurant(id) {
    const response = await fetch(API_ENDPOINT.DETAIL(id));
    const responseJson = await response.json();
    return responseJson.restaurant;
  }

  static async addReview(restaurantId, review) {
    try {
      const response = await fetch('https://restaurant-api.dicoding.dev/review', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          id: restaurantId,
          name: review.name,
          review: review.review,
        }),
      });

      if (!response.ok) {
        throw new Error('Failed to add review');
      }

      const responseData = await response.json();
      Swal.fire({
        icon: 'success',
        title: 'Success',
        text: 'Review has been added successfully!',
      });
      return responseData;
    } catch (error) {
      console.error('Error adding review:', error);
      Swal.fire({
        icon: 'error',
        title: 'Error',
        text: 'Failed to add review. Please try again later.',
      });
      throw error;
    }
  }
}

export default restaurantSource;
